// Contact Us Page
#hero-contactUsPage {
  @media (max-width: 767px) {
  }
}
#brands-gallery .gallery {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
#brands-gallery .gallery-item {
  height: 120px;
  width: 30%;
  background-color: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.15);
  margin: 2% 0;
  justify-content: center;
  display: flex;
  flex-flow: column;
  @media (max-width: 767px) {
    height: 92px;
    width: 48%;
  }
}
#brands-gallery .gallery-item:hover {
  background-color: #F0F0F0;
}
#brands-gallery .gallery-item img {
  width: 50%;
  @media (max-width: 767px) {
    width: 65%;
  }
}
#see-more-brands a.btn-from {
  border: 1px solid transparent;
  border-radius: 2px;
}
#form-description p:last-child {
  margin-top: 10px;
}
#abg_contact_us {
  max-width: 60%;
  margin: 0 auto;
  @media (max-width: 767px) {
    max-width: 100%;
  }
}

@media (min-width: 768px) {
  .page-id-7377 .elementor-element.hero-header h1.elementor-heading-title {
    font-size: 3.75rem;
  }
  .page-id-7377 .elementor-section.elementor-section-boxed .elementor-widget-text-editor.header-intro p {
    font-size: 1.5rem;
  }
  .page-id-7377 .elementor-element.brands-title h1.elementor-heading-title {
    font-weight: 700;
  }
}

.elementor-section.elementor-section-boxed.contact-form {
  .elementor-background-overlay {
    height: 115px;
  }
  .elementor-widget-text-editor p {
    font-size: 1.31rem;
    font-weight: 400;
    strong {
      font-weight: 600;
    }
  }
}

#abg_contact_us .elementor-field.elementor-select-wrapper {
  position: relative;
  float: left;
  width: 100%;
  /* IE11 hide native button (thanks Matt!) */
  select::-ms-expand {
    display: none;
  }
  &:before {
    display: none;

  }
  &:after {
    content: " ";
    right: 11px;
    top: 22px;
    position: absolute;
    pointer-events: none;
    background: url(/wp-content/themes/hello-theme-child/assets/img/select-carrot@2x.png) no-repeat center center;
    background-size: contain;
    text-indent: -1000px;
    width: 15px;
    height: 9px;
  }

  label {
    display: inline-block;
    line-height: 1;
    vertical-align: middle;
    width: 100%;
  }

  select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: block;
    height: 50px;
    float: right;
    padding: 0px 35px 0 24px;
    font-size: 16px;
    line-height: 1.75;
    color: #333;
    background-color: #fff;
    background-image: none;
    border: 1px solid #ccc;
    -ms-word-break: normal;
    word-break: normal;
    min-width: 125px;
  }
}

#abg_contact_us  #btn-contactUs {
  min-width: 200px;
}